.assign-to-shift a {
  background: #fff;
  color: #95A5A6;
  box-shadow: 1px 2px 2px #ddd;
  margin-left: 20px;
  padding: 6px 10px;
  border-radius: 3px;
  font-size: 12px;
  text-decoration: none;
}
.assign-to-shift a .dashicons {
  font-size: 12px;
  line-height: 22px;
}
.assign-form {
  background: #fff;
  padding: 35px;
  color: #000;
}
.assign-form .dropdown-menu {
  padding: 0;
}
.assign-form .dropdown-menu .popper__arrow {
  border: 0;
}
.assign-form .shift-info {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 2px 7px -1px rgba(0, 0, 0, 0.1), 0 0px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 0 20px;
  border-radius: 3px;
}
.assign-form .shift-title {
  padding-left: 5px;
}
.assign-form .c-pane-container {
  border: 0;
  box-shadow: 0 0 6px 1px #ddd;
  border-radius: 3px;
  right: 0;
  top: 70px;
}
.assign-form .c-pane-container .c-pane {
  z-index: 1;
}
.assign-form .c-header {
  background: #F9F9F9;
  height: 58px;
}
.assign-form .c-header .c-title {
  font-weight: bold !important;
  font-size: 16px !important;
}
.assign-form .c-weekdays {
  font-size: 10px !important;
  font-weight: bold !important;
  color: #000 !important;
  background: #fff;
  padding: 15px 5px 2px !important;
}
.assign-form .c-weeks {
  background: #fff;
}
.assign-form .c-weeks-rows-wrapper {
  color: #A5ACB1;
}
.assign-form .c-weeks-rows-wrapper .c-day-content {
  font-size: 11px;
  width: 2.5rem;
  height: 2.5rem;
}
.assign-form .c-weeks-rows-wrapper .c-day-content:hover {
  background: #dadada;
  cursor: pointer;
}
.att-fields .control.with-multiselect {
  position: relative;
  width: 100%;
}
.att-fields .control .dashicons {
  top: -2px;
}
.att-fields .overwrite input {
  border-color: #E7E7E7;
  border-radius: 3px;
}
.att-fields .overwrite p {
  color: #777;
  margin-top: 0;
}
.shift-assigned-list {
  border-radius: 3px;
}
.shift-assigned-list .widefat thead th,
.shift-assigned-list .widefat tfoot th {
  color: #1A9ED4 !important;
  font-weight: bold;
}
.shift-assigned-list .widefat tfoot td,
.shift-assigned-list .widefat tbody th {
  line-height: 2.5em;
}
.shift-assigned-list .widefat tbody td {
  line-height: 3em;
}
.shift-assigned-list .widefat tbody {
  background: #FAFAFA;
}
.shift-assigned-list .widefat th ul,
.shift-assigned-list .widefat th li {
  margin: 0;
}
.shift-assigned-list .widefat th li {
  display: flex;
  align-items: center;
}
.shift-assigned-list .widefat th li img {
  width: 14px;
  padding-right: 5px;
}
.shift-assigned-list .column.name {
  font-weight: bold;
  color: #222;
}
.shift-assigned-list .check-column input {
  border-color: #E7E7E7;
  box-shadow: none;
  border-radius: 3px;
}
.shift-assigned-list .check-column input:checked {
  background: #1ABC9C;
  border-color: #1ABC9C;
  border-radius: 3px;
}
.shift-assigned-list .check-column input:checked:before {
  color: #fff;
}
.shift-assigned-list .row-actions {
  padding-left: 20px;
}
