.dropdown-menu {
  position: absolute;
  background: #fff;
  color: black;
  border-radius: 3px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
  padding: 10px;
  text-align: center;
  opacity: 0;
  z-index: 2;
  left: -9999px;
}
.dropdown-menu.show {
  left: 0;
  opacity: 1;
}
.dropdown-menu ul {
  margin: 0;
}
.dropdown-menu .popper__arrow {
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
  margin: 5px;
  border-color: #fff;
}
.dropdown-menu[x-placement^="top"] {
  margin-bottom: 5px;
}
.dropdown-menu[x-placement^="top"] .tooltip-arrow {
  border-width: 5px 5px 0 5px;
  border-left-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  bottom: -5px;
  left: calc(50% - 5px);
  margin-top: 0;
  margin-bottom: 0;
}
.dropdown-menu[x-placement^="bottom"] {
  margin-top: 5px;
}
.dropdown-menu[x-placement^="bottom"] .popper__arrow {
  border-width: 0 5px 5px 5px;
  border-left-color: transparent;
  border-right-color: transparent;
  border-top-color: transparent;
  top: -5px;
  left: calc(50% - 5px);
  margin-top: 0;
  margin-bottom: 0;
}
.dropdown-menu[x-placement^="right"] {
  margin-left: 5px;
}
.dropdown-menu[x-placement^="right"] .popper__arrow {
  border-width: 5px 5px 5px 0;
  border-left-color: transparent;
  border-top-color: transparent;
  border-bottom-color: transparent;
  left: -5px;
  top: calc(50% - 5px);
  margin-left: 0;
  margin-right: 0;
}
.dropdown-menu[x-placement^="left"] {
  margin-right: 5px;
}
.dropdown-menu[x-placement^="left"] .popper__arrow {
  border-width: 5px 0 5px 5px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  right: -5px;
  top: calc(50% - 5px);
  margin-left: 0;
  margin-right: 0;
}
