.app-attendance-record {
  font-family: Helvetica;
}
.app-attendance-record .attendance-record {
  align-items: center;
  display: flex;
}
.app-attendance-record .attendance-record span {
  font-style: 18px;
  font-weight: bold;
}
.app-attendance-record .attendance-record a {
  background: #1a9ed4;
  border-radius: 3px;
  color: #fff;
  font-size: 12px;
  height: 28px;
  line-height: 29px;
  margin-left: 13px;
  text-align: center;
  text-decoration: none;
  padding: 0 10px;
}
.app-attendance-record .attendance-record a strong {
  padding-right: 5px;
}
.app-attendance-record .attendance-record-list {
  border-radius: 3px;
}
.app-attendance-record .attendance-record-list .widefat tbody td {
  padding: 20px 12px;
  text-align: center;
}
.app-attendance-record .attendance-record-list .widefat input[type=checkbox] {
  width: 20px;
  height: 20px;
}
.app-attendance-record .attendance-record-list .widefat tfoot input[type=checkbox],
.app-attendance-record .attendance-record-list .widefat thead input[type=checkbox] {
  margin-top: -4px;
}
.app-attendance-record .attendance-record-list .widefat tbody input[type=checkbox] {
  margin: 7px;
}
.app-attendance-record .attendance-record-list tbody {
  background: #FAFAFA;
}
.app-attendance-record .attendance-record-list tfoot th,
.app-attendance-record .attendance-record-list thead th {
  color: #1A9ED4;
  font-weight: bold;
  text-align: center;
}
.app-attendance-record .attendance-record-list th ul,
.app-attendance-record .attendance-record-list th li {
  margin: 0;
}
.app-attendance-record .attendance-record-list th li {
  display: flex;
  align-items: center;
}
.app-attendance-record .attendance-record-list th li img {
  width: 14px;
  padding-right: 5px;
}
.app-attendance-record .attendance-record-list .column.employee_name {
  text-align: left;
}
.app-attendance-record .attendance-record-list .column.employee_name.selected {
  color: #1A9ED4;
}
.app-attendance-record .attendance-record-list .column.employee_name a {
  color: #222;
  font-weight: normal;
}
.app-attendance-record .attendance-record-list .column.employee_name a:hover {
  color: #1A9ED4;
}
.app-attendance-record .attendance-record-list .check-column input {
  border-color: #E7E7E7;
  box-shadow: none;
  border-radius: 3px;
}
.app-attendance-record .attendance-record-list .check-column input:checked {
  background: #1ABC9C;
  border-color: #1ABC9C;
  border-radius: 3px;
}
.app-attendance-record .attendance-record-list .check-column input:checked:before {
  color: #fff;
  margin: -1px 0 0 -2px;
}
.app-attendance-record .attendance-record-list .row-actions .dropdown {
  width: 10px;
  margin: 0 auto;
}
.app-attendance-record .attendance-record-list .row-actions .dropdown-menu {
  width: 80px;
}
.app-attendance-record .attendance-record-list .row-actions .action-items li:last-child {
  margin-bottom: 0;
}
.app-attendance-record .attendance-record-list .green-circle {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background-color: #32CD32;
  margin: 0 auto;
}
.app-attendance-record .attendance-record-list .red-circle {
  border-radius: 50%;
  width: 15px;
  height: 15px;
  background-color: #ff4c4c;
  margin: 0 auto;
}
