.att-button {
  background: #1a9ed4;
  color: #fff;
  border: 0;
  border-radius: 3px;
  padding: 6px 30px;
  margin: 20px 0 15px;
  cursor: pointer;
  position: relative;
  outline: 0;
  float: right;
}
.att-button.disabled {
  background: #B3E5FC;
  cursor: default;
}
.att-button:hover :not(.disabled) {
  background: #039BE5;
}
.att-button:not(.disabled)::before {
  content: '';
  position: absolute;
  right: 10px;
  top: 50%;
  margin-top: -11px;
  width: 12px;
  height: 12px;
  border: 4px solid #84FFFF;
  border-left-color: transparent;
  border-radius: 50%;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -moz-transition-duration: 0.5s;
  -o-transition-duration: 0.5s;
  -webkit-transition-duration: 0.5s;
  transition-duration: 0.5s;
  -moz-transition-property: opacity;
  -o-transition-property: opacity;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -moz-animation-duration: 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -moz-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -moz-animation-name: rotate;
  -webkit-animation-name: rotate;
  animation-name: rotate;
  -moz-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}
.att-button:not(.disabled):after {
  content: '';
  display: inline-block;
  height: 100%;
  width: 0;
  -moz-transition-duration: 0.75s;
  -o-transition-duration: 0.75s;
  -webkit-transition-duration: 0.75s;
  transition-duration: 0.75s;
  -moz-transition-property: width;
  -o-transition-property: width;
  -webkit-transition-property: width;
  transition-property: width;
}
.att-button:not(.disabled).working {
  pointer-events: none;
  cursor: not-allowed;
}
.att-button:not(.disabled).working:not(.expand) {
  text-align: left;
}
.att-button:not(.disabled).working:before {
  -moz-transition-duration: 1s;
  -o-transition-duration: 1s;
  -webkit-transition-duration: 1s;
  transition-duration: 1s;
  filter: progid:DXImageTransform.Microsoft.Alpha(enabled=false);
  opacity: 1;
}
.att-button:not(.disabled).working:after {
  width: 20px;
}
@keyframes rotate {
0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
