.app-new-attendances {
  font-family: Helvetica;
}
.app-new-attendances .single_edit {
  line-height: 3;
}
.app-new-attendances .column .single_edit {
  line-height: 3;
  padding: 5px 10px;
  background-color: #1a9ed4;
  color: white;
  border-radius: 2px;
}
.app-new-attendances .add-new-attendance {
  align-items: center;
  display: inline;
}
.app-new-attendances .add-new-attendance span {
  font-style: 18px;
  font-weight: bold;
}
.app-new-attendances .attendance-form {
  color: #000;
}
.app-new-attendances .attendance-form .top-control {
  margin: 5px 2px;
  display: flex;
  justify-content: space-between;
}
.app-new-attendances .attendance-form .top-control .datepicker {
  display: flex;
  align-items: center;
}
.app-new-attendances .attendance-form .top-control span {
  padding-right: 10px;
  font-size: 15px;
  color: #222;
}
.app-new-attendances .attendance-form .top-control .input {
  width: 200px;
  height: 28px;
  border-radius: 3px;
  border: 1px solid #e7e7e7;
  box-shadow: none;
}
.app-new-attendances .attendance-form .top-control .make-present-absent {
  line-height: 30px;
}
.app-new-attendances .attendance-form .top-control .make-present-absent label {
  padding: 0 5px;
}
.app-new-attendances .attendance-form .top-control .make-present-absent label input[type=radio] {
  margin-right: 0;
}
.app-new-attendances .attendance-form .dropdown-menu {
  padding: 0;
}
.app-new-attendances .attendance-form .dropdown-menu .popper__arrow {
  border: 0;
}
.app-new-attendances .attendance-form .c-pane-container {
  border: 0;
  box-shadow: 0 0 6px 1px #ddd;
  border-radius: 3px;
  right: 0;
  top: 70px;
}
.app-new-attendances .attendance-form .c-pane-container .c-pane {
  z-index: 1;
}
.app-new-attendances .attendance-form .c-header {
  background: #F9F9F9;
  height: 58px;
}
.app-new-attendances .attendance-form .c-header .c-title {
  font-weight: bold !important;
  font-size: 16px !important;
}
.app-new-attendances .attendance-form .c-weekdays {
  font-size: 10px !important;
  font-weight: bold !important;
  color: #000 !important;
  background: #fff;
  padding: 15px 5px 2px !important;
}
.app-new-attendances .attendance-form .c-weeks {
  background: #fff;
}
.app-new-attendances .attendance-form .c-weeks-rows-wrapper {
  color: #A5ACB1;
}
.app-new-attendances .attendance-form .c-weeks-rows-wrapper .c-day-content {
  font-size: 11px;
  width: 2.5rem;
  height: 2.5rem;
}
.app-new-attendances .attendance-form .c-weeks-rows-wrapper .c-day-content:hover {
  background: #dadada;
  cursor: pointer;
}
.app-new-attendances .attendance-form .save {
  background: #1a9ed4;
  color: #fff;
  border: 0;
  border-radius: 3px;
  padding: 5px 25px;
  margin-top: 10px;
  font-size: 15px;
}
.app-new-attendances .attendance-form .save:hover {
  cursor: pointer;
  outline: 0;
}
.app-new-attendances .attendance-form .save:focus {
  outline: 0;
}
.app-new-attendances .new-attendance-list {
  border-radius: 3px;
}
.app-new-attendances .new-attendance-list .no-events {
  pointer-events: none;
}
.app-new-attendances .new-attendance-list tfoot,
.app-new-attendances .new-attendance-list .tablenav.bottom {
  display: none;
}
.app-new-attendances .new-attendance-list .widefat tbody td {
  text-align: center;
}
.app-new-attendances .new-attendance-list .widefat input[type=checkbox] {
  width: 20px;
  height: 20px;
}
.app-new-attendances .new-attendance-list .widefat tfoot input[type=checkbox],
.app-new-attendances .new-attendance-list .widefat thead input[type=checkbox] {
  margin-top: -4px;
}
.app-new-attendances .new-attendance-list .widefat tbody input[type=checkbox] {
  margin: 7px;
}
.app-new-attendances .new-attendance-list .widefat .input {
  width: 110px;
  height: 24px;
}
.app-new-attendances .new-attendance-list .widefat .input:disabled {
  background: #efefef;
  opacity: 0.6;
  box-shadow: none;
  border: 1px solid #ddd;
}
.app-new-attendances .new-attendance-list tfoot th,
.app-new-attendances .new-attendance-list thead th {
  color: #1A9ED4;
  font-weight: bold;
  text-align: center;
}
.app-new-attendances .new-attendance-list th ul,
.app-new-attendances .new-attendance-list th li {
  margin: 0;
}
.app-new-attendances .new-attendance-list th li {
  display: flex;
  align-items: center;
}
.app-new-attendances .new-attendance-list th li img {
  width: 14px;
  padding-right: 5px;
}
.app-new-attendances .back_btn {
  text-decoration: none;
  background-color: #1a9ed4;
  color: white;
  padding: 6px;
  margin-left: 7px;
  border-radius: 2px;
}
.app-new-attendances .column.checkin,
.app-new-attendances .column.checkout {
  width: 180px;
}
.app-new-attendances .column.checkin .input,
.app-new-attendances .column.checkout .input {
  width: 180px;
}
.app-new-attendances .global-search {
  cursor: pointer;
}
