.app-shifts {
  font-family: Helvetica;
}
.app-shifts .add-new-shift {
  align-items: center;
  display: flex;
}
.app-shifts .add-new-shift span {
  font-style: 18px;
  font-weight: bold;
}
.app-shifts .add-new-shift a {
  background: #1a9ed4;
  border-radius: 3px;
  color: #fff;
  font-size: 12px;
  height: 28px;
  line-height: 29px;
  margin-left: 13px;
  text-align: center;
  text-decoration: none;
  padding: 0 10px;
}
.app-shifts .add-new-shift a strong {
  padding-right: 5px;
}
.app-shifts .shift-list {
  border-radius: 3px;
}
.app-shifts .shift-list .widefat tbody td {
  padding: 20px 12px;
  text-align: center;
}
.app-shifts .shift-list .widefat input[type=checkbox] {
  width: 20px;
  height: 20px;
}
.app-shifts .shift-list .widefat tfoot input[type=checkbox],
.app-shifts .shift-list .widefat thead input[type=checkbox] {
  margin-top: -4px;
}
.app-shifts .shift-list .widefat tbody input[type=checkbox] {
  margin: 7px;
}
.app-shifts .shift-list tbody {
  background: #FAFAFA;
}
.app-shifts .shift-list tfoot th,
.app-shifts .shift-list thead th {
  color: #1A9ED4;
  font-weight: bold;
  text-align: center;
}
.app-shifts .shift-list th ul,
.app-shifts .shift-list th li {
  margin: 0;
}
.app-shifts .shift-list th li {
  display: flex;
  align-items: center;
}
.app-shifts .shift-list th li img {
  width: 14px;
  padding-right: 5px;
}
.app-shifts .shift-list .column.title {
  text-align: left;
}
.app-shifts .shift-list .column.title.selected {
  color: #1A9ED4;
}
.app-shifts .shift-list .column.title a {
  color: #222;
  font-weight: normal;
}
.app-shifts .shift-list .column.title a:hover {
  color: #1A9ED4;
}
.app-shifts .shift-list .column.holidays span:last-child em {
  display: none;
}
.app-shifts .shift-list .check-column input {
  border-color: #E7E7E7;
  box-shadow: none;
  border-radius: 3px;
}
.app-shifts .shift-list .check-column input:checked {
  background: #1ABC9C;
  border-color: #1ABC9C;
  border-radius: 3px;
}
.app-shifts .shift-list .check-column input:checked:before {
  color: #fff;
  margin: -1px 0 0 -2px;
}
.app-shifts .shift-list .row-actions .dropdown {
  width: 10px;
  margin: 0 auto;
}
.app-shifts .shift-list .row-actions .dropdown-menu {
  width: 80px;
}
.app-shifts .shift-list .row-actions .action-items li:last-child {
  margin-bottom: 0;
}
