.att-fields.is-2 .column:first-child {
  margin-right: 14px;
}
.att-fields.is-2 .column:last-child {
  margin-left: 14px;
}
.att-fields .field.start-time .dropdown-menu.show {
  left: 10px !important;
}
.att-fields .control {
  position: relative;
}
.att-fields .control.with-multiselect {
  position: absolute;
  width: 87%;
}
.att-fields .control .dashicons {
  position: absolute;
  top: 0;
  right: 6px;
  line-height: 2;
  color: #ddd;
}
.att-fields .control .dashicons.active {
  color: #1A9ED4;
}
.att-fields .control .input {
  width: 100%;
  height: 35px;
  border-radius: 3px;
  border-color: #E7E7E7;
  box-shadow: none;
}
.att-fields .control .input::placeholder {
  color: #DBDBDB !important;
  opacity: 1 !important;
  font-size: 14px !important;
}
.att-fields .control .input:-ms-input-placeholder {
  color: #DBDBDB !important;
  font-size: 14px !important;
}
.att-fields .control .input::-ms-input-placeholder {
  color: #DBDBDB !important;
  font-size: 14px !important;
}
.att-fields label {
  font-size: 13px;
  padding-left: 2px;
  padding-bottom: 5px;
  display: block;
  color: #000;
}
.att-fields .multiselect__content-wrapper {
  max-height: 145px !important;
}
